<template>
	<view class="rider-list">
		<view class="title">{{FullName}}</view>
		<view class="circle">
			<view class="circle_img">
				<image class="img_A" src="https://fucai.yiruit.com/UpFiles/tupian/crice.png" mode=""></image>
				<view class="circle_title" @click="handleSao">					
					<image class="img_B" src="https://fucai.yiruit.com/UpFiles/tupian/sao.png" mode="aspectFill">						
					<view class="title">扫一扫</view>
				</view>
			</view>
		</view>
		<view class="btn">
			<view class="btn_img" @click="handleMember">				
				<text>会员充值</text>
			</view>	
			<view class="btn_img" @click="handleMemberList('collectRecords')">				
				<text>领取记录</text>
			</view>	
			<view class="btn_img" @click="handleStore">
				<text>商家充值</text>
			</view>
		</view>
		
		<view class="btnList">
			<view class="btn_img">				
				<text>{{Balance}}</text>
			</view>  
			<view class="btn_img" @click="handleMemberList('memberHistory')">
				<text>余额明细</text>
			</view>
		</view>
		<!-- 弹框 -->
		<view class="background" v-if="isShow"></view>
		<view class="bag_img" v-if="isShow">
			<view class="bag_map">
				<view class="bag_btn" v-if="huiyuan === 1">会员充值</view>				
				<view class="bag_btn" v-else>商家充值</view>
				<!-- 商家给会员充值 -->
				<view class="bag_width" v-if="huiyuan === 1">
					<view class="bag_item">					
						<image src="../../static/mine/iphone.png" mode="widthFix"></image>
						<view class="inputAll">
							<input type="text" placeholder="请输入会员手机号" v-model="iPhone" maxlength="11">
						</view>
					</view>
					<view class="bag_item">					
						<image src="../../static/mine/jine.png" mode="widthFix"></image>
						<view class="inputAll"><input type="text" placeholder="请输入会员充值金额" v-model="money"></view>
					</view>
				</view>	
				<!-- 商家自己充值 -->				
				<view class="bag_width" v-else>		
					<view class="bag_item">					
						<image src="../../static/mine/jine.png" mode="widthFix"></image>
						<view class="inputAll"><input type="text" placeholder="请输入充值金额" v-model="money"></view>
					</view>
				</view>	
				
				<view class="bag_btn">					
					<view class="close" v-if="huiyuan === 1" @click="handleConfirm">确定充值</view>	
					<view class="close" v-else @click="handleStoreConfirm">确定充值</view>
					<view class="close" @click="handleClose">取消充值</view>
				</view>
			</view>			
		</view>		
	</view>
</template>
<script>
	import common from '../../common/common'
	var _self;
	export default {
		data() {
			return {	
				iPhone:'',
				money:'',
				FullName:'',
				isShow:false,
				huiyuan:1,
				Balance:'',
				userInfo:''				
			};
		},
		onLoad() {
			_self = this;		
			_self.getRuderlist()
		},
		methods: {			
			getRuderlist() {
				let { F_CompanyId } = uni.getStorageSync("riderinfo")				
				common.request({
					path: "UserOrder/getprice",
					data: F_CompanyId,
					success: res => {						
						_self.FullName = res.data.F_FullName;
						_self.Balance  =  res.data.Balance
					}
				})
			},
			handleBack(){  				
				uni.redirectTo({   // 商家登录页面
					url: '../login/merchantLogin'
				});
			},
			handleSao(){
				// 允许从相机和相册扫码
				uni.scanCode({
					success: function (res) {
						if(res.result){
							uni.navigateTo({
								url:'./merchantInfo?orderid=' + res.result
							})
						}
					},
					fail() {
						uni.showToast({
							title: '扫码失败,请重新扫码',
							icon: 'none',
							duration: 1500,
							mask: true,
						})
					}
				})				
			},
			handleMember(){
				_self.isShow = true;
				_self.huiyuan = 1;
				_self.iPhone = '';
				_self.money = ''
			},
			handleStore(){
				_self.isShow = true;
				_self.huiyuan = 2
			},
			handleMemberList(url){
				uni.navigateTo({
					url:url
				})
			},
			// inputTel(){
			// 	common.request({
			// 		path: "UserOrder/selectmember",    
			// 		data: _self.iPhone,
			// 		success: res => {
			// 			if(Object.keys(res.data).length === 0 ){
			// 				uni.showToast({
			// 					icon: 'none',
			// 					title: '该用户不存在',
			// 					duration: 1500,
			// 				})
			// 				_self.iPhone = " "
			// 				return false
			// 			}else{
			// 				uni.showToast({
			// 					icon: 'none',
			// 					title: '用户正确',
			// 					duration: 1500,
			// 				})							
			// 				_self.userInfo = res.data;						
			// 			}						
			// 		}
			// 	})
			// },			
			handleConfirm(){   // 会员充值				
				if(_self.iPhone && _self.money){	
					let { F_CompanyId } = uni.getStorageSync("riderinfo")	
					let { F_UserId } = uni.getStorageSync("riderinfo") 
					uni.showModal({
						title: '提示',
						content: '是否确认为' + _self.iPhone + '充值' + _self.money + '福豆?',
						success: function(res) {
							if (res.confirm) {
								common.request({
									path: "UserOrder/recharge",    
									data:{
										companyid:F_CompanyId,							
										amount: _self.money,
										userid: F_UserId,
										phone: _self.iPhone										
									},
									success: res => {
										if(res.code === 200){												
											_self.isShow = false
											_self.getRuderlist()
											uni.showToast({
												icon: 'none',
												title: '充值成功',
												duration: 1500,
											});
										}else{
											uni.showToast({
												icon: 'none',
												title: '该会员手机号不正确',
												duration: 1500,
											});
											_self.iPhone = ''
										}
									}
								})					
							} 
						},
						fail() {
							_self.isShow = false
						}
					})				
				}else{
					uni.showToast({
						title: '请填写信息',
						icon: 'none',
						duration: 1500,
						mask: true,
					})					
				}
			},
			handleStoreConfirm(){  // 商家充值			
				let tel = uni.getStorageSync("riderinfo").F_Account;			
				if(_self.money){
					
					
				}else{
					uni.showToast({
						title: '请填写金额',
						icon: 'none',
						duration: 1500,
						mask: true,
					})	
				}			
			},
			handleClose(){  // 取消
				_self.isShow = false; 
				_self.iPhone = '';
				_self.money = ''
			}
		}
	}
</script>
<style lang="scss" scoped>	
	.rider-list{
		position: relative;
		width: 100vw;
		height: 100vh;
		overflow: hidden;
		background-color: #ff3c36;
		margin: 0 auto;
		>.title{
			margin: 0 auto;
			font-size: 20px;
			color: #fff;
			padding-top: 80rpx;
			text-align: center;
			font-weight: bold;
		}
		.bag_img{
			position: absolute;
			left: 48%;
			top: 8%;
			background-color: #FFFFFF;
			width: 500rpx;
			height: auto;
			margin: 0 auto;
			margin-left: -250rpx;
			margin-top: 200rpx;
			border-radius: 10rpx;
			box-shadow: 0px 0px 4px #999;
			z-index: 100;
			padding: 0 20rpx;
		}
		.nav{	
			height: 10vh;
			display: flex;
			justify-content: space-between;
			flex-direction: row;
			background: #fff;
			color: #333;
			.blanck{
				width: 180rpx;
				background-color: red;
				height: 100%;
				display: flex;
				align-items: center;
				justify-content: center;
				padding-top: 10rpx;
			}
			.title{
				flex: 1;
				display: flex;
				align-items: center;
				justify-content: center;	
				padding-top: 20rpx;
				margin-left: -160rpx;
			}			
		}
		.circle{
			width: 100vw;
			background-color: #fff;
			height: auto;
			position: fixed;
			top: 13%;
			.circle_img{
				position: relative;
				height: auto;
				.img_A{
					width: 90%;
					height: 670rpx;
					position: absolute;
					top: 0;
					left: 5%;					
				}
				.circle_title{
					position: absolute;
					top: 240rpx;
					left: 36%;	
					width: 200rpx;
					height: 230rpx;
					display: flex;
					flex-direction: column;
					align-items: center;
					.img_B{
						width: 160rpx;
						height: 160rpx;
					}
					.title{
						font-size: 18px;
						color: #ff3c36;
						margin-top: 20rpx;
					}					
				}
			}			
		}
		.btn{
			position: fixed;
			left: 0;
			bottom: 10%;
			width: 100vw;
			margin: 0 auto;
			display: flex;
			flex-direction: row;
			justify-content: space-around;
			.btn_img{
				width: 27%;
				background-color: #fff;
				border-radius: 50rpx;
				height: 70rpx;
				display: flex;
				flex-direction: row;	
				align-items: center;
				justify-content: center;				
				text{
					font-size: 16px;
					color: #ff3c36;
				}
			}
		}
		.btnList{
			position: fixed;
			left: 0;
			bottom: 20%;
			width: 100vw;
			margin: 0 auto;
			display: flex;
			flex-direction: column;	
			align-items: center;
			.btn_img{
				width: 36%;	
				text-align: center;
				&:first-of-type{
					text{
						font-weight: bold;
						font-size: 20px;
					}
				}
				&:last-of-type{
					margin-top: 20rpx;
				}
				text{
					font-size: 16px;
					color: #fff;				
				}
			}
		}
	}
	.background{
		width: 100vw;
		height: 100vh;
		background-color: rgba(0,0,0,0.5);
		position: fixed;
		left: 0;
		top: 0;
		z-index: 10;
	}
	.bag_img {		
		.bag_map{
			width: 100%;
			height: auto;	
			display: flex;
			flex-direction: column;
			align-items: center;
			overflow: hidden;		
			.bag_item{
				width: 100%;
				height: 80rpx;
				line-height: 80rpx;
				overflow: hidden;
				display: flex;
				flex-direction: row;
				justify-content: flex-start;
				align-items: center;
				padding: 16rpx 0;
				border-bottom: 1px solid #eee;
				image{
					width: 50rpx;
					height: 50rpx;
					display: block;
					margin-right: 30rpx;
				}
				.inputAll{
					flex: 1;
					input{
						width: 100%;
						height: 100%;
					}
				}
			}
			.bag_width{
				width: 100%;
			}
			.bag_btn{
				width: 100%;
				height: 100rpx;
				line-height: 100rpx;
				overflow: hidden;
				display: flex;
				flex-direction: row;
				justify-content: space-around;
				font-size: 18px;
				color: #333;
				.close{
					flex: 1;
					color: #666;
					font-size: 16px;
					text-align: center;
					&:first-child{
						color: #ff3c36;
					}
				}
			}
		}		
	}
</style>
